import React from 'react';
import Increase from '../Increase/Increase';
import './Header.css';
//   

const Header = props => (
  <div className="header-warper">
    <span className="title2048">
      2048
    </span>
    <span className="game-level">
      
      {props.level}
      
    </span>
    <div className="header-right">
      <div className="score">
        <span className="">
          
          <br />
          {props.maxScore}
        </span>
      </div>
      <div className="score">
        <span className="">
          
          <br />
          {props.currentScore}
          <Increase
            increaseNum={props.increaseNum}
          />
        </span>
      </div>
    </div>
    <div className="btn-wraper">
      <button
        type="button"
        className="restart-btn"
        onClick={props.restartAction}
      >
        
      </button>
      <div className="readme-wrap">
        <div className="readme"></div>
        <div className="readme">PCW A S D     </div>
        <div className="readme">:0~3,~</div>
      </div>
    </div>
  </div>
);
export default Header;



