import * as ActionTypes from '../const/ActionTypes';
import tools from '../tools';

// 
const mapIsFull = ({ col, row }) => {
  if (col === -1 && row === -1) {
    return 1;
  }
  return 0;
};
const game = (state = {
  squareMap: tools.clearSquare(), // 
  currentScore: 0, // 
  increaseNum: 0, // 
  changedSquares: [], // 
  maxScore: 0, // 
  isDie: false, // 
  newPos: { // 
    row: -1,
    col: -1
  }
}, action) => {
  switch (action.type) {
    // 
    case ActionTypes.INIT_SQUARE_MAP: {
      // 2
      let pos = {};
      const newMap = tools.clearSquare();
      pos = tools.getNextPos(newMap);
      newMap[pos.row][pos.col] = tools.getRandomNumber();
      pos = tools.getNextPos(newMap);
      newMap[pos.row][pos.col] = tools.getRandomNumber();
      return {
        ...state,
        squareMap: newMap,
        currentScore: 0,
        increaseNum: 0,
        isDie: false,
        newPos: {
          row: -1,
          col: -1
        }
      };
    }

    // 
    case ActionTypes.MOVE_BY_DIRECTIONS: {
      const { key } = action;
      const oldMap = state.squareMap.slice();
      const newPos = {
        row: -1,
        col: -1
      };

      // 
      // 
      const moveResult = tools.moveByDirections(key, oldMap);
      const {
        newMap,
        increaseNum,
        willGenerateNew,
        changedSquares
      } = moveResult;

      // 
      const { row, col } = tools.getNextPos(newMap);

      // rowcol
      //   
      if (mapIsFull({ row, col })) {
        // game over  
        if (tools.judgeDie(newMap)) {
          return {
            ...state,
            maxScore: state.maxScore < state.currentScore
              ? state.currentScore
              : state.maxScore,
            isDie: true
          };
        }
        return state;
      }
      //  
      if (willGenerateNew) {
        newMap[row][col] = tools.getRandomNumber();
        newPos.row = row;
        newPos.col = col;
      }

      return {
        ...state,
        squareMap: newMap,
        increaseNum,
        currentScore: state.currentScore + increaseNum,
        changedSquares,
        newPos: {
          row,
          col
        },
        isDie: false
      };
    }

    //  0~3
    case ActionTypes.DESTROY_SQUARE: {
      const newMap = state.squareMap.slice();
      [1, 2, 3].forEach(() => {
        const { row, col } = tools.getNextPos();
        newMap[row][col] = 0;
      });
      return {
        ...state,
        squareMap: newMap,
        isDie: false
      };
    }

    default: return state;
  }
};
export default game;



