import React from 'react';
import './DieMessage.css';

// 
const DieMessage = props => (
  <div>
    {props.isDie
      ? (
        <div className="game-die">
          GAME OVER 
          {props.level}
          
          {
          props.haveBomb
            ? ''
            : ''
        }
        </div>
      )
      : null
    }
  </div>
);
export default DieMessage;



